import * as React from 'react';
import { Component } from 'react';
import { connect } from 'react-redux';
import * as actionCreators from '../store/actionCreators';

import data from "../data/data";
import dynastyData from "../data/dynastyData";
import animation from "../utils/animation";
import coordinate from "../utils/coordinate";
import Chart from './components/chart';
import Header from './components/header';
import Progress from './components/progress';

import {
  itemHeight,
  itemMargin,
  itemWidth
} from './constants';
import {
  HomeWrap
} from './style';

interface IProps {
  dynasty: string;
  isPaused: boolean;
  itemList: IItem[];
  speed: number;
  size: string;
  year: number;

  setItemList: (itemList: any[]) => void;
  setSize: (size: string) => any;
  setYear: (year: number) => any;
  speedUp: () => any;
  speedDown: () => any;
  startOrPauseAnimation: (isPaused: boolean) => any;
}

const vHeight = document.documentElement.clientHeight - 135;
const vWidth = document.body.clientWidth;
const originX = Math.floor((vWidth - 36) / 2)
const originY = 10

let maxHeight = vHeight;
let nowSpeed = 4;

class Home extends Component<IProps, {}> {
  constructor(props: any) {
    super(props);
    const rowCount = Math.floor((vWidth - itemMargin) / (itemWidth + itemMargin));
    coordinate.setRowcount(rowCount);
    this.getItemList = this.getItemList.bind(this);
  }
  public componentDidMount(): void {
    animation.tick(() => {
      if (nowSpeed !== this.props.speed) {
        animation.setTimeSpan(Math.floor(100 / (nowSpeed / 4)));
        nowSpeed = this.props.speed;
      }
      if (this.props.year >= data.endYear || this.props.year < data.startYear) {
        this.props.startOrPauseAnimation(false)();
        return;
      }
      this.props.setYear(this.props.year + 1);
    })
  }
  public render() {
    return (
      <HomeWrap>
        <Header
          year={this.props.year}
          dynasty={getDynasty(this.props.year)}
          personCount={this.getItemList(this.props.year).length}
          size={this.props.size}
          isPaused={this.props.isPaused}
          speed={this.props.speed}
          setSize={this.props.setSize}
          speedUp={this.props.speedUp}
          speedDown={this.props.speedDown}
          startOrPauseAnimation={this.props.startOrPauseAnimation} />
        <Chart
          height={getChartHeight(this.getItemList(this.props.year).length) > vHeight ? getChartHeight(this.getItemList(this.props.year).length) : vHeight}
          size={this.props.size}
          year={this.props.year}
          dynasty={getDynasty(this.props.year)}
          itemList={this.getItemList(this.props.year)} />
        <Progress year={this.props.year} endYear={data.endYear} startYear={data.startYear} setYear={this.props.setYear} />
        <div className="top-right">开发中</div>
      </HomeWrap>
    );
  }

  private getItemList(year: number) {
    const displayedData = data.data.filter((v: any, i: number, a: any[]) => {
      if (v.hasOwnProperty('x') && v.hasOwnProperty('y')) {
        if (!a[i].isRelease && (year < v.birthYear || year > v.deadYear)) {
          coordinate.releaseQueue(i)
          a[i].isRelease = true
        }
      }
      if (year >= v.birthYear && year <= v.deadYear) {
        if (!a[i].hasOwnProperty('x')) {
          const iCoordinate = coordinate.getCoordinate(i);
          a[i].id = i
          a[i].x = originX + (iCoordinate[0] || 0) * (itemWidth + itemMargin)
          a[i].y = originY + (iCoordinate[1] || 0) * (itemHeight + itemMargin)
          a[i].isRelease = false
          a[i].color = getColor()
        }
        return true
      }
      return false
    });
    return displayedData;
  }
}

function getColor() {
  const colors = ['#9d2932', '#efdeb0', '#789262', '#494166',
    '#a88462', '#b9b612', '#177cb0', '#424b50', '#b36d61',
    '#c3272b', '#a98175', '#f30', '#76664d',
    '#415065', '#ed5736']
  return colors[Math.floor(colors.length * Math.random())]
}

function getDynasty(year: number) {
  for (const dynasty of dynastyData) {
    if (year >= dynasty.startYear && year <= dynasty.endYear) {
      return dynasty.name;
    }
  }
  return '';
}

function getChartHeight(count: number) {
  let maxColCount = Math.floor((vWidth - itemMargin) / (itemWidth + itemMargin));
  maxColCount = maxColCount % 2 ? maxColCount : maxColCount - 1;
  let degree = Math.ceil(Math.sqrt(count));
  degree = degree % 2 ? degree : degree + 1;
  degree = degree > maxColCount ? maxColCount : degree;
  let rowCount = degree;
  if (count / degree > degree) {
    rowCount = Math.ceil(count / degree);
  }
  const height = 10 + (rowCount + 1) * (itemMargin + itemHeight);
  maxHeight = height > maxHeight ? height : maxHeight;
  return maxHeight;
}

interface IItem {
  color: string;
  name: string;
  x: number;
  y: number;
}
interface IState {
  dynasty: string;
  isPaused: boolean;
  itemList: IItem[];
  speed: number;
  size: string;
  year: number;
}

const mapState = (state: IState) => {
  const { dynasty, isPaused, itemList, size, speed, year } = state;
  return {
    dynasty,
    isPaused,
    itemList,
    size,
    speed,
    year
  }
};

const mapDispatch = (dispatch: any, ownState: any) => ({
  setSize(size: string) {
    return () => {
      dispatch(actionCreators.setSize(size));
    }
  },
  speedUp() {
    dispatch(actionCreators.speedUp());
  },
  speedDown() {
    dispatch(actionCreators.speedDown());
  },
  startOrPauseAnimation(isPaused: boolean) {
    return () => {
      if (isPaused) {
        dispatch(actionCreators.start());
        animation.start();
      } else {
        dispatch(actionCreators.pause());
        animation.pause();
      }
    }
  },
  setYear(year: number) {
    dispatch(actionCreators.setYear(year));
  },
  setItemList(itemList: any[]) {
    dispatch(actionCreators.setItemList(itemList));
  }
})

export default connect(mapState, mapDispatch)(Home);



