export default {
  _cache: {},
  _coordinateCache: {},
  _queue: [],
  _rowCount: 0,
  calculateCoordinate(i) {
    if (i === 0) {
      this._coordinateCache[i] = [0, 0];
    }
    if (this._coordinateCache[i]) {
      return this._coordinateCache[i];
    }
    const index = i + 1;
    let degree = Math.floor(Math.sqrt(index));
    degree = degree % 2 ? degree : degree - 1;
    if (index === degree * degree) {
      degree = degree - 2;
    }
    degree = (degree + 2) > this._rowCount ? this._rowCount - 2 : degree;
    if (index <= degree * (degree + 2) && degree + 2 <= this._rowCount) {
      const temp = index - degree * degree;
      const x = temp % 2 ? Math.floor((degree + 2) / 2) : -Math.floor((degree + 2) / 2);
      this._coordinateCache[i] = [x, Math.floor((temp + 1) / 2) - 1];
    } else {
      const remainder = index % (degree + 2);
      let x = 0;
      let y = 0;
      if (remainder === 0) {
        x = Math.floor((degree + 2) / 2);
        y = Math.floor(index / (degree + 2)) - 1;
      } else {
        x = Math.floor(remainder / 2);
        x = remainder % 2 ? x : -x;
        y = Math.floor(index / (degree + 2));
      }
      this._coordinateCache[i] = [x, y];
    }

    return this._coordinateCache[i];
  },
  getCoordinate(itemIndex) {
    if (this._cache[itemIndex]) {
      return this._cache[itemIndex];
    }
    const len = this._queue.length;
    for (let i = 0; i < len; i++) {
      if (this._queue[i] === -1) {
        this._queue[i] = itemIndex;
        this._cache[itemIndex] = this.calculateCoordinate(i);
        return this._cache[itemIndex];
      }
    }
    this._queue.push(itemIndex);
    this._cache[itemIndex] = this.calculateCoordinate(this._queue.length - 1);
    return this._cache[itemIndex];
  },
  releaseCache() {
    this._cache = {};
    this._coordinateCache = {};
  },
  releaseQueue(itemIndex) {
    for (let i = 0; i < this._queue.length; i++) {
      if (this._queue[i] === itemIndex) {
        this._queue[i] = -1;
        return true;
      }
    }
    return false;
  },
  setRowcount(rowCount) {
    rowCount = rowCount % 2 ? rowCount : rowCount - 1;
    this._rowCount = rowCount;
  }
};


