import {
  PAUSE,
  SET_ITEM_LIST,
  SET_PERSON_COUNT,
  SET_SIZE,
  SET_YEAR,
  SPEED_DOWN,
  SPEED_UP,
  START
} from './constants';

const defaultState = {
  dynasty: '唐朝',
  isPaused: true,
  itemList: [],
  personCount: 0,
  size: 'small',
  speed: 4,
  year: -550
};

export default function (state = defaultState, action) {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case SPEED_UP:
      if (newState.speed < 16) {
        newState.speed *= 2;
      }
      return newState;
    case SPEED_DOWN:
      if (newState.speed > 1) {
        newState.speed /= 2;
      }
      return newState;
    case PAUSE:
      newState.isPaused = true;
      return newState;
    case START:
      newState.isPaused = false;
      return newState;
    case SET_SIZE:
      newState.size = action.size;
      return newState;
    case SET_YEAR:
      newState.year = action.year;
      return newState;
    case SET_ITEM_LIST:
      newState.itemList = action.itemList;
      return newState;
    case SET_PERSON_COUNT:
      newState.personCount = action.count;
      return newState;
  }
  return state;
}


