import {
  PAUSE,
  SET_ITEM_LIST,
  SET_PERSON_COUNT,
  SET_SIZE,
  SET_YEAR,
  SPEED_DOWN,
  SPEED_UP,
  START
} from './constants';

export const speedUp = () => ({
  type: SPEED_UP
});

export const speedDown = () => ({
  type: SPEED_DOWN
});

export const pause = () => ({
  type: PAUSE
});

export const start = () => ({
  type: START
});

export const setSize = (size) => ({
  size,
  type: SET_SIZE
});

export const setYear = (year) => ({
  type: SET_YEAR,
  year
});

export const setItemList = (itemList) => ({
  itemList,
  type: SET_ITEM_LIST
});

export const setPersonCount = (count) => ({
  count,
  type: SET_PERSON_COUNT
});


