export default {
  _lastUpdateTime: 0,
  _pause: true,
  _tick: null,
  _timeSpan: 100,

  start() {
    this._pause = false;
    this.update();
  },
  update() {
    if (this._pause) {
      return;
    }
    if (this._lastUpdateTime === 0) {
      this._lastUpdateTime = new Date().getTime();
    }
    const now = new Date().getTime()
    if (now - this._lastUpdateTime >= this._timeSpan) {
      this._lastUpdateTime = now;
      if (typeof this._tick === 'function') {
        this._tick();
      }
    }
    window.requestAnimationFrame(() => {
      this.update();
    });
  },
  tick(callback) {
    if (typeof callback === 'function') {
      this._tick = callback;
      this._tick();
    }
  },
  pause() {
    this._pause = true;
  },
  setTimeSpan(timeSpan) {
    this._timeSpan = timeSpan;
  }
};



