import * as React from 'react';
import {
  getChineseCount,
  getChineseYear
} from '../../../utils/labelFormatter';
import {
  ControlIcon,
  ControlPanel,
  HeaderWrap,
  Info,
  InfoWrap,
  SizePanel,
  Title
} from './style';

interface IProps {
  year: number;
  dynasty: string;
  personCount: number;
  size: string
  isPaused: boolean,
  setSize: any,
  speed: number,
  speedDown: any,
  speedUp: any,
  startOrPauseAnimation: any
}

function Header(props: IProps) {
  return (
    <HeaderWrap>
      <SizePanel>
        {/* <span className={props.size === 'big' ? 'active big' : 'big'} onClick={props.setSize('big')}>大</span>
        <span className={props.size === 'mid' ? 'active mid' : 'mid'} onClick={props.setSize('mid')}>中</span>
        <span className={props.size === 'small' ? 'active small' : 'small'} onClick={props.setSize('small')}>小</span> */}
      </SizePanel>
      <Title>
        覽概據數物人史歷國中
      </Title>
      <Info>
        <InfoWrap>
          <span>{getChineseCount(props.personCount).split('').reverse().join('')}</span>
          <span>{props.dynasty.split('').reverse().join('')}</span>
          <span>{getChineseYear(props.year).split('').reverse().join('')}</span>
        </InfoWrap>
      </Info>
      <ControlPanel>
        <ControlIcon title='加速' onClick={props.speedUp}>
          <i className='iconfont'>&#xe65f;</i>
        </ControlIcon>
        <ControlIcon className='reverse' title='播放/暂停' onClick={props.startOrPauseAnimation(props.isPaused)}>
          {
            props.isPaused ? <i className='iconfont'>&#xe63d;</i> : <i className='iconfont'>&#xe76a;</i>
          }
        </ControlIcon>
        <ControlIcon title='减速' onClick={props.speedDown}>
          <i className='iconfont'>&#xe65e;</i>
        </ControlIcon>
        <ControlIcon className='speed'>
          <i className='iconfont speed-icon'>&#xe634;</i>{props.speed}
        </ControlIcon>
      </ControlPanel>
    </HeaderWrap>
  )
}

export default Header;



