import * as React from 'react';
import { Component } from 'react';
import {
  getChineseYear
} from '../../../utils/labelFormatter';
import {
  Cursor,
  ProgressShadow,
  ProgressUI,
  ProgressWrap
} from './style';

interface IProps {
  startYear: number;
  endYear: number;
  year: number;

  setYear: (year: number) => void;
}

interface IStates {
  isMouseDown: boolean;
  left: number;
  cursorRight: number;
  width: number;
}

class Progress extends Component<IProps, IStates> {
  constructor(props: any) {
    super(props);
    this.onMouseDown = this.onMouseDown.bind(this);
    this.onMouseMove = this.onMouseMove.bind(this);
    this.onMouseUp = this.onMouseUp.bind(this);
    this.initPrivateParams = this.initPrivateParams.bind(this);
    window.addEventListener('mousemove', this.onMouseMove);
    window.addEventListener('mouseup', this.onMouseUp);
    window.addEventListener('touchmove', this.onMouseMove);
    window.addEventListener('touchend', this.onMouseUp);

    this.state = {
      cursorRight: 0,
      isMouseDown: false,
      left: 40,
      width: 800
    }
  }
  public render() {
    return (
      <ProgressWrap ref={this.initPrivateParams}
        onDragEnter={onDrag}
        onDragOver={onDrag}>
        <span className='left'>{getChineseYear(this.props.endYear).split('').reverse().join('')}</span>
        <span className='right'>{getChineseYear(this.props.startYear).split('').reverse().join('')}</span>
        <ProgressShadow />
        <Cursor
          onMouseDown={this.onMouseDown}
          onTouchStart={this.onMouseDown}
          style={{ right: this.getRight(this.props.year) - 8 + 'px' }} />
        <ProgressUI
          style={{ width: (this.getRight(this.props.year) - 36) + 'px' }} />
      </ProgressWrap>
    )
  }

  public componentWillUnmount() {
    window.removeEventListener('mousemove', this.onMouseMove);
    window.removeEventListener('mouseup', this.onMouseUp);
    window.removeEventListener('touchmove', this.onMouseMove);
    window.removeEventListener('touchend', this.onMouseUp);
  }

  private initPrivateParams(e: any) {
    this.setState({
      left: e.offsetLeft,
      width: e.offsetWidth
    }, () => {
      const { endYear, startYear, year } = this.props;
      const yearSpan = year - startYear;
      const contentWidth = this.state.width - 2 * 40;
      const right = Math.floor(contentWidth * (yearSpan / (endYear - startYear))) + 40;
      this.setState({
        cursorRight: right
      })
    })
  }

  private onMouseDown(e: any) {
    if (this.state.isMouseDown) {
      return;
    }
    this.setState({
      isMouseDown: true
    })
  }

  private onMouseUp(e: any) {
    if (!this.state.isMouseDown) {
      return;
    }
    this.setState({
      isMouseDown: false
    })
  }

  private onMouseMove(e: any) {
    const { endYear, startYear } = this.props;
    const { left, width } = this.state;
    if (this.state.isMouseDown) {
      let cursorRight = width - ((e.pageX || e.touches[0].pageX) - left);
      let isMouseDown = true;
      if (cursorRight <= 40) {
        cursorRight = 40;
        isMouseDown = false;
      } else if (cursorRight > width - 40) {
        cursorRight = width - 40;
        if (cursorRight > width - 35) {
          isMouseDown = false;
        }
      }
      if (!isMouseDown) {
        this.setState({
          isMouseDown
        });
        return;
      }
      const timeSpan = cursorRight - 40;
      const year = Math.floor((endYear - startYear) * (timeSpan / (width - 2 * 40)) + startYear);
      this.props.setYear(year);
    }
  }

  private getRight(year: number) {
    const { endYear, startYear } = this.props;
    const yearSpan = year - startYear;
    const contentWidth = this.state.width - 2 * 40;
    const right = Math.floor(contentWidth * (yearSpan / (endYear - startYear))) + 40;
    return right;
  }
}

function onDrag(e: any) {
  e.persist();
  e.preventDefault();
  return;
}

export default Progress;


