import * as React from 'react';
import {
  ChartItem,
  ChartQuote,
  ChartWrap
} from './style';

import {
  // itemHeight,
  itemWidth
} from '../../constants'

interface IProps {
  size: string;
  year: number;
  dynasty: string;
  itemList: any[];
  height: number;
}

function getItemSizeStyle() {
  return {
    borderRadius: `${Math.ceil(itemWidth / 2)}px`,
    // height: `${itemHeight}px`,
    width: `${itemWidth}px`
  }
}

function Chart(props: IProps) {
  return (
    <ChartWrap style={{ height: props.height + 'px' }}>
      {
        props.itemList.map((v: any) => (
          <ChartItem
            key={v.id}
            style={{
              ...getItemSizeStyle(), ...{ top: v.y + 'px', left: v.x + 'px', backgroundColor: v.color }
            }}>
            {v.nameCN}
          </ChartItem>
        ))
      }
      <ChartQuote>
        * 人物数据均来自<a href='https://projects.iq.harvard.edu/chinesecbdb' target='_blank'>《中國歷代人物傳記資料庫（CBDB）》</a>
      </ChartQuote>
    </ChartWrap>
  )
}

export default Chart;


