import styled from 'styled-components';

export const ProgressWrap = styled.div `
position: fixed;
width: 80%;
left: 10%;
bottom: 30px;
padding: 30px 40px 20px 40px;
box-sizing: border-box;
background: rgba(255, 255, 255, .6);
span {
  position: absolute;
  top: 10px;
  font-size: 13px;
  &.left {
    left: 10px;
  }
  &.right {
    right: 10px;
  }
}
`;

export const ProgressShadow = styled.div `
width: 100%;
height: 2px;
background-color: #aba9a9;
border-radius: 1px;
`;

export const ProgressUI = styled.div `
position: absolute;
top: 29px;
right: 40px;
width: 200px;
height: 4px;
background-color: #242220;
border-radius: 2px;
`;

export const Cursor = styled.div `
position: absolute;
top: 23px;
right: 235px;
width: 8px;
height: 8px;
background-color: #fff;
border: 4px solid #242220;
border-radius: 8px;
box-shadow: 0 5px 5px rgba(0,0,0,0.4); 
z-index: 1;
cursor: pointer;
`;


