import styled from 'styled-components';

export const ChartWrap = styled.div `
position: relative;
box-sizing: border-box;
margin: 13px 18px;
box-shadow: 0 0 0 1px #242220, 0 0 0 4px #fff, 0 0 0 8px #242220;
`;

export const ChartItem = styled.div `
position: absolute;
text-align: center;
padding: 5px;
box-sizing: border-box;
color: #fff;
max-height: 58px;
overflow: hidden;
`;

export const ChartDesc = styled.div `
min-height: 153px;
box-sizing: border-box;
padding: 20px;
border-right: 1px solid #242220;
border-bottom: 1px solid #242220;
background-color: rgba(255, 255, 255, .6);
z-index: 1;
writing-mode: vertical-rl;
user-select: none;
p {
  line-height: 1.5;
}
`;

export const ChartQuote = styled.blockquote `
position: absolute;
bottom: 5px;
left: 5px;
writing-mode: vertical-rl;
font-size: 12px;
a {
  color: #000;
}
`;



