const numToCh = (strNum) => {
  switch (strNum) {
    case '0':
      return '零';
    case '1':
      return '一';
    case '2':
      return '二';
    case '3':
      return '三';
    case '4':
      return '四';
    case '5':
      return '五';
    case '6':
      return '六';
    case '7':
      return '七';
    case '8':
      return '八';
    case '9':
      return '九';
    default:
      return '';
  }
};

export const getChineseYear = (year) => {
  let result = '';
  if (year < 0) {
    result = '公元前';
    year = -year;
  }
  const strYear = year + '';
  for (const val of strYear) {
    result += numToCh(val);
  }
  return result + '年';
};

function* getUnit() {
  yield '';
  yield '十';
  yield '百';
  yield '千';
  yield '万';
}

export const getChineseCount = (count) => {
  let result = '人';
  if (count < 0 || count === 0) {
    return '零人';
  }
  let allZero = true;
  const strCount = count + '';
  const arrCount = strCount.split('');
  arrCount.reverse();

  const unitGen = getUnit();
  for (let i = 0; i < arrCount.length; i++) {
    const val = arrCount[i];
    if (val !== '0') {
      allZero = false;
    }
    const unit = unitGen.next();
    if (unit.done) {
      window.console.log('unit error');
      break;
    }
    if (val === '1' && i === 1 && arrCount.length === 2) {
      result = unit.value + result;
    } else if (val !== '0' || !allZero) {
      if (val === '0') {
        result = numToCh(val) + result;
      } else {
        result = numToCh(val) + unit.value + result;
      }
    }
  }
  return result;
};



