import styled from 'styled-components';

export const HeaderWrap = styled.div `
position: relative;
`;

export const Title = styled.h1 `
padding-top: 20px;
text-align: center;
font-size: 32px;
margin-bottom: 5px
`;

export const ControlPanel = styled.div `
overflow: hidden;
text-align: center;
`;

export const ControlIcon = styled.div `
display: inline-block;
font-size: 13px;
padding: 5px;
cursor: pointer;
user-select: none;

&.speed {
  width: 25px;
  margin-right: -35px;
}

.iconfont {
  font-size: 13px;
  color: #333;

  &.speed-icon {
    margin-right: -2px;
  }
}
&.reverse {
  transform: rotateY(180deg);
}
&.lf {
  float: left;
}
&.lr {
  float: right;
}
`;

export const SizePanel = styled.div `
position: absolute;
top: 12px;
left: 10px;

span {
  display: inline-block;
  margin-right: 5px;
  color: #888;
  cursor: pointer;
  vertical-align: bottom;
  border-radius: 2px;

  &:hover {
    background-color: #eee;
  }

  &.active {
    background-color: #666;
    color: #fff;
  }
}

.small {
  font-size: 12px;
}

.mid {
  margin-right: 8px;
  font-size: 16px;
}

.big {
  font-size: 20px;
}
`;

export const Info = styled.div `
text-align: center;
margin-bottom: 10px;
`;

export const InfoWrap = styled.div `
display: inline-block;
span {
  display: inline-block;
  margin: 0 5px;
}
`;


