import styled from 'styled-components';

export const HomeWrap = styled.div`
position: relative;
min-height: 100%;

.top-right {
  position: absolute;
  background-color: #9d2932;
  color: #fff;
  top: 0;
  right: 0;
  padding: 5px 5px 6px 5px;
  writing-mode: vertical-rl;
}
`;


